/* eslint global-require: off, no-console: off, promise/always-return: off */

import path from 'path';
import {
  app,
  BrowserWindow,
  dialog,
  ipcMain,
  Menu,
  nativeImage,
  net,
  protocol,
  shell,
  Tray,
} from 'electron';
import { autoUpdater, UpdateDownloadedEvent } from 'electron-updater';
import log from 'electron-log';
import { spawn } from 'child_process';
import { resolveHtmlPath } from './util';

protocol.registerSchemesAsPrivileged([
  { scheme: 'app', privileges: { secure: true, standard: true } },
]);

app.commandLine.appendSwitch('--no-sandbox');
app.disableHardwareAcceleration();

if (process.platform === 'win32') app.setAppUserModelId(app.getName());

if (!app.requestSingleInstanceLock()) {
  app.quit();
  process.exit(0);
}

process.env.ELECTRON_DISABLE_SECURITY_WARNINGS = 'true';

let mainWindow: BrowserWindow | null = null;

if (process.env.NODE_ENV === 'production') {
  const sourceMapSupport = require('source-map-support');
  sourceMapSupport.install();
}

const isDebug =
  process.env.NODE_ENV === 'development' || process.env.DEBUG_PROD === 'true';

if (isDebug) {
  require('electron-debug')();
}

const installExtensions = async () => {
  const installer = require('electron-devtools-installer');
  const forceDownload = !!process.env.UPGRADE_EXTENSIONS;
  const extensions = ['REACT_DEVELOPER_TOOLS'];

  return installer
    .default(
      extensions.map((name) => installer[name]),
      forceDownload
    )
    .catch(console.log);
};

let minToTray = false;
const createWindow = async () => {
  if (isDebug) {
    await installExtensions();
  }

  const RESOURCES_PATH = app.isPackaged
    ? path.join(process.resourcesPath, 'assets')
    : path.join(__dirname, '../../assets');

  const getAssetPath = (...paths: string[]): string => {
    return path.join(RESOURCES_PATH, ...paths);
  };

  mainWindow = new BrowserWindow({
    show: false,
    width: 800,
    height: 600,
    icon: getAssetPath('icon.png'),
    webPreferences: {
      devTools: !app.isPackaged,
      preload: app.isPackaged
        ? path.join(__dirname, 'preload.js')
        : path.join(__dirname, '../../.erb/dll/preload.js'),
    },
    titleBarStyle: 'hidden',
    titleBarOverlay: {
      color: '#2f3241',
      symbolColor: '#74b1be',
      height: 32,
    },
    frame: false,
    minWidth: 800,
    minHeight: 600,
  });

  mainWindow.loadURL(resolveHtmlPath('index.html'));

  mainWindow.on('ready-to-show', () => {
    if (!mainWindow) {
      throw new Error('"mainWindow" is not defined');
    }
    if (process.env.START_MINIMIZED) {
      mainWindow.minimize();
    } else {
      mainWindow.show();
    }
  });

  mainWindow.on('closed', () => {
    mainWindow = null;
  });

  mainWindow.webContents.setWindowOpenHandler((edata) => {
    shell.openExternal(edata.url);
    return { action: 'deny' };
  });

  const tray = new Tray(
    nativeImage.createFromPath(
      app.isPackaged ? './resources/assets/icon.png' : './assets/icon.png'
    )
  );

  let quitFromTray = false;
  tray.setToolTip('MSFS2020 Map Enhancement');
  tray.setContextMenu(
    Menu.buildFromTemplate([
      {
        label: 'Show App',
        click() {
          mainWindow!.show();
        },
      },
      {
        label: 'Quit',
        click() {
          quitFromTray = true;
          app.quit();
        },
      },
    ])
  );

  tray.on('double-click', () => {
    mainWindow!.show();
  });

  mainWindow.on('close', async (e) => {
    if (minToTray) {
      if (!quitFromTray) {
        e.preventDefault();
        mainWindow!.hide();
        e.returnValue = false;
      } else {
        tray.destroy();
        e.returnValue = true;
      }
    } else {
      tray.destroy();
      e.returnValue = true;
    }

    if (e.returnValue) {
      const request = net.request({
        method: 'POST',
        url: 'http://localhost:42098/api/app/stop',
      });

      request.on('response', (response) => {
        console.log(`Exit response: ${response.statusCode}`);
      });
      request.end();
    }
  });
};

app.on('window-all-closed', () => {
  if (process.platform !== 'darwin') {
    app.quit();
  }
});

app
  .whenReady()
  .then(() => {
    createWindow();
    app.on('activate', () => {
      if (mainWindow === null) createWindow();
    });
  })
  .catch(console.log);

ipcMain.handle('start-backend', () => {
  const imageServer = spawn('core.exe', {
    cwd: app.isPackaged
      ? path.join(path.dirname(app.getAppPath()), './core/release/')
      : '../core/bin/Debug/net6.0/',
    stdio: 'ignore',
  });

  imageServer.on('close', (code) => {
    log.log(`Image server closed`, code);
  });

  imageServer.on('error', (err) => {
    log.error(`Image server Failed to start process`, err);
  });

  const result = imageServer.pid !== undefined;
  console.log('Image server pid', imageServer.pid);
  return result;
});

ipcMain.handle('select-cache-file', async () => {
  const returnValue = await dialog.showOpenDialog(mainWindow!, {
    title: 'Choose cache file',
    defaultPath: 'cache.db',
    properties: ['openFile', 'promptToCreate'],
  });

  return returnValue.filePaths[0];
});

ipcMain.handle('select-license-file', async () => {
  const returnValue = await dialog.showOpenDialog(mainWindow!, {
    title: 'Choose license file',
    filters: [{ extensions: ['lic'], name: 'license file' }],
    properties: ['openFile'],
  });

  return returnValue.filePaths[0];
});

ipcMain.handle('open-url', async (event, url) => {
  await shell.openExternal(url);
});

ipcMain.handle('set-min-to-tray', async (event, toTray) => {
  minToTray = toTray;
});

ipcMain.handle('min-to-tray', async () => {
  if (minToTray) {
    mainWindow?.close();
  }
});

ipcMain.handle('check-update', async () => {
  autoUpdater.autoDownload = true;
  autoUpdater.autoInstallOnAppQuit = false;
  const updateCheckResult = await autoUpdater.checkForUpdates();
  return updateCheckResult?.updateInfo;
});

ipcMain.handle('get-app-version', () => {
  return app.getVersion();
});

ipcMain.handle('update-channel', (e, channel) => {
  console.log('Setting update channel to ', channel);
  autoUpdater.channel = channel;
});

autoUpdater.on('update-downloaded', (event: UpdateDownloadedEvent) => {
  const dialogOpts = {
    type: 'info',
    buttons: ['Restart', 'Later'],
    title: 'Application Update',
    message: event.releaseName ?? '',
    detail:
      'A new version has been downloaded. Restart the application to apply the updates.',
  };

  dialog
    .showMessageBox(dialogOpts)
    .then((returnValue) => {
      if (returnValue.response === 0) {
        autoUpdater.quitAndInstall();
      }
    })
    .catch(console.log);
});
